/* RESET         */ /* RESET         */ /* RESET         */ /* RESET         */ /* RESET         */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  word-break: break-all;
}

a {
  text-decoration: none;
  display: block;
}

li {
  list-style: none;
}

img {
  display: block;
}

input:focus,
select:focus {
  outline: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

body {
  overflow-x: hidden;
}

/* font-face */
@font-face {
  font-family: noto;
  font-weight: 400;
  src: url(./asset/font/NotoSansCJKkr-Regular.otf);
  src: url(./asset/font/NotoSansCJKkr-Regular.otf) format('opentype'),
  url(./asset/font/NotoSansCJKkr-Regular.ttf) format('truetype')
}
@font-face {
  font-family: noto;
  font-weight: 800;
  src: url(./asset/font/NotoSansCJKkr-Black.otf);
  src: url(./asset/font/NotoSansCJKkr-Black.otf) format('opentype'),
  url(./asset/font/NotoSansCJKkr-Black.ttf) format('truetype')
}
@font-face {
  font-family: noto;
  font-weight: 700;
  src: url(./asset/font/NotoSansCJKkr-Bold.otf);
  src: url(./asset/font/NotoSansCJKkr-Bold.otf) format('opentype'),
  url(./asset/font/NotoSansCJKkr-Bold.ttf) format('truetype')
}
@font-face {
  font-family: noto;
  font-weight: 500;
  src: url(./asset/font/NotoSansCJKkr-Medium.otf);
  src:  url(./asset/font/NotoSansCJKkr-Medium.otf) format('opentype'),
  url(./asset/font/NotoSansCJKkr-Medium.ttf) format('truetype');
}
@font-face {
  font-family: noto;
  font-weight: 300;
  src: url(./asset/font/NotoSansCJKkr-DemiLight.otf);
  src: url(./asset/font/NotoSansCJKkr-DemiLight.otf) format('opentype'),
  url(./asset/font/NotoSansCJKkr-Light.ttf) format('truetype')
}
@font-face {
  font-family: noto;
  font-weight: 200;
  src: url(./asset/font/NotoSansCJKkr-Light.otf);
  src: url(./asset/font/NotoSansCJKkr-Light.otf) format('opentype'),
  url(./asset/font/NotoSansCJKkr-Light.ttf) format('truetype')
}
