/* font reset */
body,
header,
footer,
nav,
section,
main,
figcaption,
figure,
summary,
details,
aside,
article,
div,
p,
a,
h1,
h2,
h3,
h4,
h5,
h6,
form,
input,
button,
select,
option {
  font-family: -apple-system, BlinkMacSystemFont, noto, sans-serif;
  font-weight: 400;
  color: #191919;
}
.main_color {
  color: #5ac29f;
}
.bold_text {
  font-weight: 700;
}

