.slider_pc_photo .photo_slick {
  position: relative;
  width: 1000px;
  min-width: 900px;
  margin: 0 auto;
}

.slider_pc_photo .photo_slick .slick-track > div {
  height: 560px;
  padding: 160px 0;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.slider_pc_photo .photo_slick .slick-active {
  -webkit-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
}

.slider_pc_photo .photo_slick .slick-center {
  -webkit-transform: scale(1.7);
          transform: scale(1.7);
  position: relative;
  z-index: 7;
}

.slider_pc_photo .photo_slick .slick-slide:not(.slick-center) {
  opacity: 0.5;
}

.slider_pc_photo .review_slick {
  font-size: 5rem;
  width: 137rem;
  margin: 0 auto;
}

.slider_pc_photo .review_slick h3 img {
  width: auto;
  margin: 0 auto;
}

.slider_with_who {
  width: 137rem;
  margin: 8.5rem auto 18.5rem auto;
}

.slider_with_who .with_page_wrap {
