  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.slider_with_who .with_who_page2 .with_who_img {
  width: 27%;
}

.slider_with_who .with_who_img img {
  width: 25rem;
}

.slider_with_who button.slick-arrow::before {
  display: none;
}

.slick_dots li {
  width: 0.6rem;
  height: 0.6rem;
  border-radius: 50%;
  margin: 0;
}

.slick-dots {
  bottom: -1.5rem;
}

.slick-dots li {
  width: 0.6rem;
  height: 0.6rem;
  margin: 0 0.7rem;
}

.slick-dots li button {
  padding: 0;
  width: auto;
  height: auto;
}

.slick-dots li button::before {
  width: 0.6rem;
  height: 0.6rem;
  font-size: 0.6rem;
  line-height: 0.6rem;
}

.slider_mo_photo .review_slick {
  margin-top: 2rem;
}

